import styles from "./Menu.module.css";
import {
  useToggleMenu,
  useMenuIsOpen,
  usePurchasesInfo,
  useSetStatus,
  useClearCart,
} from "../store/store";
import { useNavigate } from "react-router-dom";
import { HashLink } from "react-router-hash-link";

export const Menu = () => {
  const toggleMenu = useToggleMenu();
  const menuIsOpen = useMenuIsOpen();
  const navigate = useNavigate();
  const purchaseInfo = usePurchasesInfo();
  const hashLinkHandler = (hash) => {
    toggleMenu();
    const section = document.getElementById(hash);
    section.scrollIntoView({ behavior: "smooth" });
  };
  const setStatus = useSetStatus();
  const clearCart = useClearCart();

  return (
    <>
      <div
        className={`${styles.blackout}
        ${menuIsOpen ? styles.openBlackout : styles.closeBlackout}`}
      ></div>
      <div
        className={`${styles["wrapper"]} ${menuIsOpen ? styles.open : ""}`}
        onClick={() => toggleMenu()}
      >
        <div
          className={styles["container"]}
          onClick={(e) => e.stopPropagation()}
        >
          <div className={styles.header}>
            <h2 className={styles.title}>JULIIAZHIVAEVA</h2>
            <button onClick={() => toggleMenu()}>
              <svg
                width="19"
                height="19"
                viewBox="0 0 19 19"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  fillRule="evenodd"
                  clipRule="evenodd"
                  d="M0.292453 0.292453C0.479954 0.105186 0.734119 0 0.99912 0C1.26412 0 1.51829 0.105186 1.70579 0.292453L17.7058 16.2925C17.804 16.384 17.8828 16.4944 17.9375 16.6171C17.9922 16.7397 18.0215 16.8722 18.0239 17.0064C18.0263 17.1407 18.0016 17.2741 17.9513 17.3986C17.901 17.5231 17.8261 17.6362 17.7312 17.7312C17.6362 17.8261 17.5231 17.901 17.3986 17.9513C17.2741 18.0016 17.1407 18.0263 17.0064 18.0239C16.8722 18.0215 16.7397 17.9922 16.6171 17.9375C16.4944 17.8828 16.384 17.804 16.2925 17.7058L0.292453 1.70579C0.105186 1.51829 0 1.26412 0 0.99912C0 0.734119 0.105186 0.479954 0.292453 0.292453Z"
                  fill="#FBE0F3"
                />
                <path
                  fillRule="evenodd"
                  clipRule="evenodd"
                  d="M17.7057 0.292453C17.8929 0.479954 17.9981 0.734119 17.9981 0.99912C17.9981 1.26412 17.8929 1.51829 17.7057 1.70579L1.70565 17.7058C1.51608 17.8824 1.26535 17.9786 1.00628 17.974C0.747217 17.9694 0.500036 17.8645 0.316819 17.6813C0.133602 17.4981 0.0286525 17.2509 0.0240816 16.9918C0.0195106 16.7327 0.115675 16.482 0.292315 16.2925L16.2923 0.292453C16.4798 0.105186 16.734 0 16.999 0C17.264 0 17.5181 0.105186 17.7057 0.292453Z"
                  fill="#FBE0F3"
                />
              </svg>
            </button>
          </div>
          <nav className={styles.navigation}>
            <button onClick={() => toggleMenu()}>
              <HashLink smooth to="/#training">
                ТРЕНИРОВКИ
              </HashLink>
            </button>
            <button onClick={() => toggleMenu()}>
              <HashLink smooth to="/#diet">
                РАЦИОН
              </HashLink>
            </button>
            <button onClick={() => toggleMenu()}>
              <HashLink smooth to="/#questions">
                ВОПРОС-ОТВЕТ
              </HashLink>
            </button>
            <button onClick={() => hashLinkHandler("contacts")}>
              <HashLink smooth to="/#contacts">
                КОНТАКТЫ
              </HashLink>
            </button>
            {Object.keys(purchaseInfo).length > 0 ? (
              <button
                onClick={() => navigate("/calc")}
                className={styles["header__nav-link"]}
              >
                РАССЧИТАТЬ КБЖУ
              </button>
            ) : (
              ""
            )}
          </nav>
          <button
            className={styles["exit-btn"]}
            onClick={() => {
              setStatus("guest");
              localStorage.removeItem("token");
              navigate("/");
              window.scrollTo(0, 0);
              clearCart();
              toggleMenu();
            }}
          >
            ВЫЙТИ ИЗ АККАУНТА
          </button>
        </div>
      </div>
    </>
  );
};
